function unix(dt: number, type: any): string {
  const milliseconds: number = dt * 1000;
  const data: Date = new Date(milliseconds);

  const result: string =
    type == "weekday"
      ? data.toLocaleString("ru-RU", { weekday: "long" })
      : type == "month"
      ? data.toLocaleString("ru-RU", { month: "long" })
      : type == "day"
      ? data.toLocaleString("ru-RU", { day: "numeric" })
      : "";

  return result;
}

export default unix;
