
import { defineComponent } from "vue";
import { mapGetters, mapState, useStore } from "vuex";

export default defineComponent({
    name: "MainContent",
    data() {
        return {
            data: null,
            difference: 0,
        }
    },
    computed: {
        ...mapGetters(['getFullWeather']),
        description(): string {
            const geter = this.$store.getters.getFullWeather.current.weather[0].description;
            return `${geter.toLowerCase()}`;
        },
    },
    mounted() {
        setInterval(() => {
            try {
                this.difference = new Date(Math.abs(+new Date(this.getFullWeather.date_local) - +new Date())).getMinutes();
            } catch (error) {
                this.difference = 0;
            }
        }, 1000);
    },

})
