
import { defineComponent } from "vue"; 

export default defineComponent({
    name: "notFound",
    data() {
        return {
            msg: ["404 - ошибка данных, город/страна не найдена или проверьте интернет",
                "404 - data error, city/country not found or check internet"]
        }
    },
    props: ["lang"],
    computed: {
        message() {
            return this.lang == "en" ? this.msg[1] : this.msg[0]
        }
    },
});
