import { createStore } from "vuex";
import axios from "axios";

export const store = createStore({
  state: {
    apiKeyWheather: "8a2ecd9340b86edf2d9344b98cd07696",
    apiKeyTime: "LVDM24763CKU",
    getFullWeather: null,
  },
  getters: {
    getFullWeather: (state) => state.getFullWeather,
    getDailyWeather(state){
      let arr: [null | object] = state.getFullWeather ? state.getFullWeather['daily'] : [null];
      arr.pop();
      return arr
    }
  },
  mutations: {
    getWeather(state, payload) {
      state.getFullWeather = payload;
    },
  },
  actions: {
    async getWeather({ commit, state }, city: string) {
      try {
        const geoData = await axios.get(
          `http://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKeyWheather}`
        );

        const geo = await geoData.data[0];
        const { lat, lon } = geo;

        const cityName: string = geo.local_names[localStorage.lang];
        localStorage.mtr = cityName;

        const weatherData = await axios.get(
          `https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=minutely,hourly&units=metric&lang=${localStorage.lang}&appid=${state.apiKeyWheather}`
        );

        const weather = weatherData.data;
        const pressure_hPa = weather.current.pressure;
        const pressure_mmHg = pressure_hPa * 0.75006375541921; 

        const pressure_power =
          740 >= pressure_mmHg
            ? "низкое!"
            : 740 < pressure_mmHg && pressure_mmHg <= 750
            ? "пониженное"
            : 750 < pressure_mmHg && pressure_mmHg <= 770
            ? "нормальное"
            : 770 < pressure_mmHg && pressure_mmHg <= 780
            ? "повышенное"
            : "высокое!";

        const wind_deg = weather.current.wind_deg;
        const wind_direction =
          0 <= wind_deg && wind_deg < 22.5
            ? "север"
            : 22.5 <= wind_deg && wind_deg < 67.5
            ? "северо-восток"
            : 67.5 <= wind_deg && wind_deg < 112.5
            ? "восток"
            : 112.5 <= wind_deg && wind_deg < 157.5
            ? "юго-восток"
            : 157.5 <= wind_deg && wind_deg < 202.5
            ? "юг"
            : 202.5 <= wind_deg && wind_deg < 247.5
            ? "юго-запад"
            : 247.5 <= wind_deg && wind_deg < 292.5
            ? "запад"
            : 292.5 <= wind_deg && wind_deg < 337.5
            ? "северо-запад"
            : "север";

        const wind_speed = weather.current.wind_speed;
        const wind_power =
          wind_speed < 0.2
            ? "штиль"
            : 0.3 <= wind_speed && wind_speed < 1.6
            ? "очень лёгкий"
            : 1.6 <= wind_speed && wind_speed < 3.4
            ? "лёгкий"
            : 3.4 <= wind_speed && wind_speed < 5.5
            ? "слабый"
            : 5.5 <= wind_speed && wind_speed < 8
            ? "умеренный"
            : 8 <= wind_speed && wind_speed < 10.8
            ? "свежий"
            : 10.8 <= wind_speed && wind_speed < 13.9
            ? "сильный"
            : 13.9 <= wind_speed && wind_speed < 17.2
            ? "крепкий"
            : 17.2 <= wind_speed && wind_speed < 20.8
            ? "очень крепкий"
            : 20.8 <= wind_speed && wind_speed < 24.5
            ? "шторм"
            : 24.5 <= wind_speed && wind_speed < 28.5
            ? "сильный шторм"
            : 28.5 <= wind_speed && wind_speed < 32.7
            ? "жестокий шторм"
            : "ураган";

        const uvi_index = weather.current.uvi;
        const uvi_power =
          uvi_index < 3
            ? "низкий"
            : 3 <= uvi_index && uvi_index < 6
            ? "умеренный"
            : 6 <= uvi_index && uvi_index < 8
            ? "высокий"
            : 8 <= uvi_index && uvi_index < 11
            ? "очень высокий"
            : "чрезмерный";

        weather.current = {
          ...weather.current,
          pressure_mmHg,
          pressure_power,
          wind_direction,
          wind_power,
          uvi_power,
        };

        const time = await axios.get(
          `http://api.timezonedb.com/v2.1/get-time-zone?key=${state.apiKeyTime}&format=json&by=position&lat=${lat}&lng=${lon}`
        );

        const foreignDate: Date = new Date(time.data.formatted);
        const foreignHours: String | Number =
          foreignDate.getHours() < 10
            ? `0${foreignDate.getHours()}`
            : foreignDate.getHours();
        const foreignMinutes: String | Number =
          foreignDate.getMinutes() < 10
            ? `0${foreignDate.getMinutes()}`
            : foreignDate.getMinutes();
        const foreignTime: String = foreignHours + ":" + foreignMinutes;
        const date_local = new Date().toUTCString();
        const date_foreign = foreignDate.toUTCString();

        const weatherObj: Object = {
          ...weather,
          cityName,
          foreignTime,
          date_local,
          date_foreign,
        };
        console.log(weatherObj);

        commit("getWeather", weatherObj);
      } catch (error) {
        console.log(error);
        commit("getWeather", "error");
      }
    },
  },
  modules: {},
});
